<template>
  <span>{{ address }}</span>
</template>

<script>
import { defineComponent, ref, computed } from 'vue'
import { useUser } from 'composable/user'

export default defineComponent({
  name: 'Address',
  props: {
    data: {
      type: Object,
      default: () => ({
        address1: undefined,
        suite: undefined,
        city: undefined,
        state: undefined,
        zip: undefined
      })
    }
  },
  setup(props) {
    const { me } = useUser()
    const formatAddress = ref({
	    default: [['address1', 'suite', 'city'], ['state', 'zip'], ['country']],
	    NL: [['address1', 'suite'], ['zip', 'city'], ['country']]
    })
    const keyRule = computed(() => props.data.country || me.value.country_code)
    const rules = computed(() => {
      return formatAddress.value[keyRule.value] || formatAddress.value.default
    })
    const address = computed(() => {
      let results = []
      rules.value.forEach(rule => {
        const result = []
        rule.forEach(key => {
          const item = (props.data && props.data[key]) || undefined
          if (item) result.push(item)
        })
        results.push(result.join(' '))
      })

      results = (!!results.length && results.join(', ')) || ''

      if (['NL'].includes(keyRule.value) && !!props.data.suite) {
        results = results.replace(` ${props.data.suite}`, ` - ${props.data.suite}`)
      }

      return results
    })

    return {
      rules,
      address
    }
  }
})
</script>
